import React from 'react';
import { Table } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import Watched from './Watched';


function Cart(props) {

    let reduxState = useSelector((state) => state);
    console.log(reduxState);
    let dispatch = useDispatch();

    return (
        <div>
            <Table responsive="xl">
                <thead>
                    <tr>
                        <th>#</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        reduxState.reducer.map((a, i) => {
                            return (
                                <tr key={i}>
                                    <td>{a.id}</td>
                                    <td>{a.title}</td>
                                    <td>{a.content}</td>
                                    <td>{a.price}</td>
                                    <td>{a.quan}</td>
                                    <td><button onClick={() => {
                                        dispatch({ type: '-', payload: a.id })
                                    }}>+</button>
                                        <button onClick={() => {
                                            dispatch({ type: '-', payload: a.id })
                                        }}>-</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
            {
                reduxState.reducer2 === true ?
                    (
                        <div className='my-alert'>
                            <p>   20%</p>
                            <button onClick={() => {
                                dispatch({ type: 'close-alert' })
                            }}></button>
                        </div>
                    )
                    : null

            }
            <Watched __product={props._product}></Watched>

        </div >
    )
}
