import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import { combineReducers, createStore } from 'redux';
import Basket from './basket.js';

let basket = Basket;

//   !!!!!! . ""
function reducer(x = basket, p) {
  let copy = [...x];

  if (p.type === '-') {
    let found = copy.findIndex((y) => { return y.id === p.payload.id });
    if (found >= 0) {
      copy[found].quan++;
      return copy;
    }
    else {
      copy.push(p.payload);
      return copy
    }
  }
  console.log(copy[p.payload]);
  if (p.type === '-') {
    copy[p.payload].quan++;
    return copy
  }
  else if (p.type === '-') {
    copy[p.payload].quan--;
    if (copy[p.payload].quan < 0) {
      copy[p.payload].quan = 0;
    }
    return copy
  }
  else {
    return x
  }
}

function reducer2(x = true, p) {
  if (p.type === 'close-alert') {
    x = false;
    return x
  }
  else {
    return x
  }
}

let reduxStore = createStore(combineReducers({ reducer, reducer2 }));

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={reduxStore}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>
);

serviceWorkerRegistration.register();
// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
